@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300&family=Inter:wght@200&family=Varela&display=swap');


* {
    margin: 0;
    padding: 0;
    font-family: 'Varela', sans-serif;
    box-sizing: border-box;

}



.link {
    text-decoration: none;
    color: #000;
}

/* Auth Button */
.main-navbar .top-navbar .auth-btn {
    border-radius: 10px;
    margin-right: 5px;
    background-color: transparent;
    border: none;
}

.main-navbar .top-navbar .auth-btn-filled {
    border-radius: 10px;
    margin-right: 5px;
    background-color: #33cabb;
    border: none;
}

/* Auth Button */

/* navbar css start */
.main-navbar {
    border-bottom: 1px solid #ccc;
}

.main-navbar .top-navbar {
    border-bottom: 1px solid #ccc;
    background-color: #ffff;
    padding-top: 10px;
    padding-bottom: 10px;
}

.main-navbar .top-navbar .brand-flex {
    display: flex;
    flex-direction: row;
}

.main-navbar .top-navbar .brand-name {
    margin-left: 100px;
    color: #949292;
    margin-top: 2px;
}

.main-navbar .top-navbar .brand-name-color {
    margin-left: 0px;
    color: #33cabb;
    margin-top: 2px;
}

.main-navbar .top-navbar .form-control {
    border: 1px solid rgb(182, 179, 179);
    border-radius: 30px;
}

.main-navbar .top-navbar .btn {
    border: 1px solid rgb(182, 179, 179);
    border-radius: 30px;
}

.main-navbar .top-navbar .nav-link {
    margin-right: 0px;
    color: #949292;
    font-size: 16px;
    font-weight: 500;
}

.main-navbar .top-navbar .nav-item .fa {
    color: #2874f0;
}

.main-navbar .top-navbar .dropdown-menu {
    padding: 0px 0px;
    border-radius: 0px;
}

.main-navbar .top-navbar .dropdown-menu .dropdown-item {
    padding: 8px 16px;
    border-bottom: 1px solid #ccc;
    font-size: 14px;
}

.main-navbar .top-navbar .dropdown-menu .dropdown-item i {
    width: 20px;
    text-align: center;
    color: #2874f0;
    font-size: 14px;
}

.main-navbar .navbar {
    padding: 0px;
    background-color: #ddd;
}

.main-navbar .navbar .nav-item .nav-link {
    padding: 8px 20px;
    color: #000;
    font-size: 15px;
}

.main-navbar .navbar-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

@media only screen and (max-width: 600px) {
    .main-navbar .top-navbar .nav-link {
        font-size: 12px;
        padding: 8px 10px;
    }
}

/* navbar css end */


/* side menu start */
.menu {
    font-size: 25px;
    cursor: pointer;
}

.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    left: 0;
    background-color: #e6e4e4;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 40px;
}

.offcanvas-body p {
    padding: 0.9rem;
}

.offcanvas-body p:hover {
    background-color: #121212;
    color: white;
    border: none;
    transition: all 0.2s ease-in-out;
    transform: scale(1.1);
}

.btn-container button {
    font-size: 17px;

}

.font-icon {
    display: flex;
    align-items: center;
    justify-content: center;
}

.font-icon1 {
    font-size: 24px;
    color: grey;
    width: 35px;
    /* padding: 1rem; */
    /* margin: 8px; */

}

NavLink {
    font-size: 14px;
    text-decoration: none;
    border: none;
    color: grey;

}

@media (min-width: 1025px) {
    .h-custom {
        height: 100vh !important;
    }
}



.sidenav a {
    padding: 0px 8px;
    text-decoration: none;
    font-size: 14px;
    color: #000000;
    display: block;
    transition: 0.3s;

}

.sidenav a:hover {
    color: #f1f1f1;
}

.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 34px;
    margin-left: 50px;
}

.sidenav h3 {
    margin-left: 8px;
    margin-bottom: 28px;
}

@media screen and (max-height: 450px) {
    .sidenav {
        padding-top: 15px;
    }

    .sidenav a {
        font-size: 18px;
    }
}

/* side menu ends */



